export default function ( notice ){

  const noticeValidator = () =>{
    console.log( "noticeValidator" )
	return validLength(1, 1000, notice.value.contents)
  }
  
  //computed
  //eN
  //S`FbNo^{^gp
  const resNoticeVal = computed( noticeValidator )

  //YW[A\bh
  return{
	resNoticeVal
  }
}