<template>
	<div>
		<div style="display:flex;">
			<div>
				<div class="inputItemDiv"><div class="inputTitleDiv">ID</div><input :class="accountCssClass" :readonly="readonly" type="text" v-model="account.userid"></div>
				<div class="inputItemDiv" v-show="!resAccountIdVal"><div class="inputErrorTitleDiv"></div><span class="validateError">45</span></div>
				<div class="inputItemDiv" v-show="!resAccountIdEmailVal"><div class="inputErrorTitleDiv"></div><span class="validateError"></span></div>


				<div class="inputItemDiv"><div class="inputTitleDiv"></div><input class="input" type="text" v-model="account.name"></div>
				<div class="inputItemDiv" v-show="!resNameVal"><div class="inputErrorTitleDiv"></div><span class="validateError">45</span></div>


				<div class="inputItemDiv"><div class="inputTitleDiv">CD</div><input class="input" type="text" v-model="account.supplierscd"></div>
				<div class="inputItemDiv" v-show="!resSuppliersCdVal"><div class="inputErrorTitleDiv"></div><span class="validateError">45</span></div>


				<div class="inputItemDiv"><div class="inputTitleDiv"></div><input class="input" type="text" v-model="account.companyname"></div>
				<div class="inputItemDiv" v-show="!resCompanyNameVal"><div class="inputErrorTitleDiv"></div><span class="validateError">100</span></div>

				<div class="inputItemDiv"><div class="inputTitleDiv"></div><input class="input" type="text" v-model="account.department"></div>
				<div class="inputItemDiv" v-show="!resDepartmentVal"><div class="inputErrorTitleDiv"></div><span class="validateError">100</span></div>

				<div class="inputItemDiv"><div class="inputTitleDiv"></div><input class="input" type="text" v-model="account.watermark"></div>
				<div class="inputItemDiv" v-show="!resWaterMarkVal"><div class="inputErrorTitleDiv"></div><span class="validateError">10</span></div>

				<div class="inputItemDiv"><div class="inputTitleDiv inputAreaTitleDiv"></div><textarea class="inputArea" v-model="account.remark"></textArea></div>
				<div class="inputItemDiv" v-show="!resRemarkVal"><div class="inputErrorTitleDiv"></div><span class="validateError">255</span></div>
			</div>
			
			<div style="margin-left:10px;">
				<div class="inputItemDiv"><div class="inputTitleDiv"></div><input type="checkbox" v-model="account.isnoticemail" /></div>
				<div class="inputItemDiv"><div class="inputTitleDiv"></div><input class="input" type="text" v-model="password"></div>
				<div class="inputItemDiv" v-show="!resPasswordVal"><div class="inputErrorTitleDiv"></div><span class="validateError">10</span></div>
				<div class="inputItemDiv" v-show="!resExitAccountVal"><div class="inputErrorTitleDiv"></div><span class="validateError">ID</span></div>

			</div>
	
		</div>
		<br>
		<br>
		<div style="text-align:center;width:100%;">
			<button  type="button" class="button" @click='cancel'></button>
			<button :disabled="resAllVal"  type="button" class="button" @click='regist'></button>
		</div>

	</div>

</template>

<script>
import accountValidator from '../composables/AccountValidator'
import accountRepository from '../composables/AccountRepository'
import { ref } from 'vue'
export default ({
  name: 'EditAccount',
  props: {
    id: String
  },

  setup(props, context){
  
    const accountCssClass = ref("input")
    const readonly = ref(false)
    
    const resExitAccountVal = ref( true )
    const account = ref({id:'',name:'',companyId:'', isnoticemail:true})
    const password = ref("")
  
    //AccountRepository
    const { getAccountById, registAccount, checkExistUserId} = accountRepository()

    
        //
    const getCallback = ( result, data ) =>{
      if( result != "success"){
        context.emit( 'setMessage', result )
      }else{
        accountCssClass.value = "inputread"
        readonly.value = true
        account.value = data
      }
    }
    
    //ID
    if( props.id != null ){
      getAccountById( props.id, getCallback  )
    }
    


    //accountValidator
    //resName:
    //resPricceVal:
    //resAllVal:
    
    const{
      resRemarkVal,
      resAccountIdVal,
      resNameVal,
      resCompanyNameVal,
      resDepartmentVal,
      resWaterMarkVal,
      resPasswordVal,
      resSuppliersCdVal,
      resAccountIdEmailVal,
      resAllVal
    } = accountValidator( account, password )

	const checkExistUserIdCallback = (result) =>{
      if( result == "ok"){
        registAccount( account.value, regCallback )
      }else{
        resExitAccountVal.value = false
      }
    }


    //
    const regist = () => {
      if( account.value.id == null || account.value.id.length == 0 ){
        account.value.password = password.value
      }else{
        if( password.value != null && password.value.length != 0 ){
          account.value.password = password.value
        }
      }
      checkExistUserId( account.value, checkExistUserIdCallback )
    }
    

    
    //( accountRepositoryregistaccount)
    const regCallback = ( result, data ) =>{
      if( result == "success"){
        context.emit( 'setMessage', "" )
        
        //
        context.emit( 'addRegistedAccountToList', data )
      }else{
        context.emit( 'setMessage', result )
      }
      
      context.emit( 'setIsEdit', false )
    }
    
    // 
    const cancel = () => {
      console.log("cancel")
      context.emit( 'setIsEdit', false )
    }
    
    
    //template
    return{
      account,
      regist,
      cancel,
      resRemarkVal,
      resAccountIdVal,
      resNameVal,
      resCompanyNameVal,
      resDepartmentVal,
      resWaterMarkVal,
      resPasswordVal,
      resSuppliersCdVal,
      resAccountIdEmailVal,
      resAllVal,
      resExitAccountVal,
      accountCssClass,
      readonly,
      password
    }
  }

})

</script>



