import { ref, computed } from 'vue'

//iXgn
export default function ( accountList ){
  
  //Aref
  const searchAccountId = ref('')
  const searchAccountName = ref('')
  const searchCompanyName = ref('')
  
  //searchQueryXYWbNNAVue.jscomputed@\gp
  //ili[returnOgpo
  const matchAccount = computed(() => {
    return accountList.value.filter(account => {
      
      if( searchAccountId.value != "" && !account.userid.includes(searchAccountId.value) ){
        return false
      }
      console.log("1")
      if(searchAccountName.value != "" && !account.name.includes( searchAccountName.value )){
        return false
      }
      console.log("2")
      console.log(account.companyname)
      if(searchCompanyName.value != "" && !account.companyname.includes( searchCompanyName.value )){
        return false
      }
      console.log("3")
      return true
    })
  })

  //YCuA\bh
  return{
    searchAccountId,
    searchAccountName,
    searchCompanyName,
    matchAccount
  }

}