import { computed } from 'vue'
import { validLength } from '../api/checker'

export default function ( catalog, file ){

  //`FbN
  const remarkValidator = () =>{
	return validLength(1, 10, catalog.value.remark)
  }

 //[X`FbN
  const releaseDateValidator = () =>{
	return validLength(1, 8, catalog.value.releasedate)

    //if( !validYearMonth( catalog.value.releasedate ) ) return false;
	//return validRequire( catalog.value.releasedate )
  }

  
  //S`FbN
  const allValidator = () =>{
    var res = ( computed( remarkValidator ).value )
    
    if( res ){
      res = ( computed( releaseDateValidator ).value )
    }
    
    if( res ){
      if( file.value == null ){
        res = false
      }else{
        res = true
      }
    }
    console.log( res )
    if( res ){
      return false
    }else{
      return true
    }
  }
  
  

  //computed
  //eN
  //S`FbNo^{^gp
  const resRemarkVal = computed( remarkValidator )
  const resReleaseDateVal = computed( releaseDateValidator )
  const resAllVal = computed( allValidator )

  //YW[A\bh
  return{
	resRemarkVal,
	resReleaseDateVal,
	resAllVal
  }
}